import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import Utils from "utils";
import moment from "moment";
import { DATE_FORMAT_DD_MM_YYYY } from "constants/DateConstant";
const employeeAgingService = {};
employeeAgingService.getReport = async data => {
  const { path, method } = API_CONSTANTS.GET_EMPLOYEE_AGING_REPORT;
  try {
    const response = await service({
      url: path,
      method,
      data,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response.records;
  } catch (e) {
    console.error("Error in getting the employee list.", e);
  }
};
employeeAgingService.preparedHeadersForPDF = (data = []) => data.map(col => col.key);

employeeAgingService.preparedDataForExport = (data = [], allData, filterVisibleColumns) => {
  return data.map(record => {
    const _item = {
      employeeId: record.employeeId || "-",
      hrmsId: record.hrmsId || "-",
      employeeName: record.employeeName || "-",
      employeeLocation: record.employeeLocation || "-",
      functionalGroupName: record?.functionalGroupName || "-",
      employeeTotalExperience: record.employeeTotalExperience || "-",
      parentAccountName: record.parentAccountName || "-",
      parentAccountAllocationStatus: record.parentAccountAllocationStatus || "-",
      parentAccountAging: record?.parentAccountAging || "-",
      extendedReservedEndDate: record?.extendedReservedEndDate
        ? moment(record.extendedReservedEndDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "",
      parentAccountStatusStartDate: record?.parentAccountStatusStartDate
        ? moment(record.parentAccountStatusStartDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      proposedAccountAllocationStatus: record.proposedAccountAllocationStatus || "-",
      proposedAccountStatusStartDate: record?.proposedAccountStatusStartDate
        ? moment(record.proposedAccountStatusStartDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      proposedAccountName: record.proposedAccountName || "-",
      proposedAccountAging: record.proposedAccountAging || "-",
      reservedForAccountName: record?.reservedForAccountName || "-",
    };
    if (filterVisibleColumns) {
      Object.keys(_item).forEach(itemKey => {
        if (!filterVisibleColumns[itemKey]) {
          delete _item[itemKey];
        }
      });
    }
    if (allData) {
      return {
        ..._item,
        employeeEmail: record?.employeeEmail || "-",
        employeeDesignation: record?.employeeDesignation || "-",
        employeeMobileNumber: record?.employeeMobileNumber || "-",
        employeeRole: record?.employeeRole || "-",
        employeeDateOfJoining: record?.employeeDateOfJoining
          ? moment(record.employeeDateOfJoining).format(DATE_FORMAT_DD_MM_YYYY)
          : "-",
        employeeStatus: record?.employeeStatus || "-",
        employeeInactiveStatusDate: record?.employeeInactiveStatusDate
          ? moment(record.employeeInactiveStatusDate).format(DATE_FORMAT_DD_MM_YYYY)
          : "-",
        cclName: record?.cclName || "-",
        cclEmail: record?.cclEmail || "-",
        allocationStatus: record?.allocationStatus || "-",
        subStatus: record?.subStatus || "-",
        reservedForAccountName: record?.reservedForAccountName || "-",
        extendedReservedEndDate: record?.extendedReservedEndDate
          ? moment(record?.extendedReservedEndDate).format(DATE_FORMAT_DD_MM_YYYY)
          : "",
        subStatusStartDate: record?.subStatusStartDate
          ? moment(record.subStatusStartDate).format(DATE_FORMAT_DD_MM_YYYY)
          : "-",
        subStatusEndDate: record?.subStatusEndDate
          ? moment(record.subStatusEndDate).format(DATE_FORMAT_DD_MM_YYYY)
          : "-",
      };
    }
    return _item;
  });
};
export default employeeAgingService;
