import { MenuFoldOutlined, SearchOutlined, QuestionCircleTwoTone } from "@ant-design/icons";
import { Button, Col, Drawer, Form, Row, Select, Tooltip, PageHeader, Popover, Tabs } from "antd";
import LocationPicker from "components/shared-components/LocationPicker";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import TableMenuItem from "components/shared-components/TableMenuItem";
import {
  CHILDREN,
  DATE_FORMAT,
  LIGHT_NAVY_BLUE_COLOR,
  SEARCH,
  SORT_DIRECTIONS,
  SELECT_TEXT,
  REPORTS_DESCRIPTION,
} from "constants/CommonConstants";
import { MENU_ITEMS, MENU_ITEMS_TYPES } from "constants/EmployeeLoginDetails";
import { DATA_TABLE_COLUMNS_AGING_REPORT, FILE_NAME, FORM_NAME } from "constants/EmployeeAgingReportConstant";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useMemo, useRef, useState } from "react";
import { useSelector } from "react-redux";
import Utils from "utils";
import FileHandler from "utils/fileHandler";
import { FILTER_FORM_FIELDS } from "../../../../constants/CommonConstants";
import employeeAgingService from "services/EmployeeAgingReport";
import { ACCOUNT_TYPE } from "constants/FinanceReportsConstants";
import { ALL_DATA_TABLE_COLUMNS_EMPLOYEESTATUS_REPORT } from "../../../../constants/EmployeeAgingReportConstant";
import EmployeeAgingReportSummary from "./EmployeeAgingReportSummary";
import EmployeeAgingReportVisual from "./EmployeeAgingReportVisual";
const ALL = "All";

const { Option } = Select;

const initialVisibleColumns = {
  employeeId: true,
  employeeName: true,
  employeeLocation: true,
  functionalGroupName: true,
  parentAccountName: true,
  proposedAccountName: true,
  hrmsId: true,
  parentAccountAllocationStatus: true,
  parentAccountStatusStartDate: true,
  proposedAccountStatusStartDate: true,
  proposedAccountAging: true,
  employeeTotalExperience: true,
  parentAccountAging: true,
  proposedAccountAllocationStatus: true,
  extendedReservedEndDate: true,
};
const defaultSearchParams = {
  city: [ALL],
  account: [ALL],
};

const BenchReport = () => {
  const getMessage = useIntlMessage();
  const [open, setOpen] = useState(false);
  const allLocations = useSelector(state => state.myTeams.allLocations);
  const [filteredEmployeeAgingReportData, setFilteredEmployeeAgingReportData] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [sortedInfo, setSortedInfo] = useState({});
  const [EmployeeAgingReportData, setEmployeeAgingReportData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [visibility, setVisibility] = useState(initialVisibleColumns);
  const [isMenuVisible, setIsMenuVisible] = useState(false);
  const accountsList = useSelector(store => store.myTeams.accountsList);
  const [searchParams, setSearchParams] = useState(defaultSearchParams);
  const [openPopOver, setOpenPopOver] = useState(false);

  let searchInput = useRef(null);
  let currentVisibleTableData = [];
  const [form] = Form.useForm();
  // function disabledDate(current) {
  //   return current && current > moment().endOf("day");
  // }

  const getColumnSearchProps = field => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <SearchFilterDropdown
        setSelectedKeys={setSelectedKeys}
        selectedKeys={selectedKeys}
        confirm={confirm}
        clearFilters={clearFilters}
        handleSearch={handleSearch}
        dataIndex={field.key}
        field={field}
        searchInput={searchInput}
        handleReset={handleReset}
        label={getMessage(field.label)}
        dataSource={EmployeeAgingReportData}
      />
    ),
    filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />,
    onFilter: (value, record) => {
      if (field.type === "date") {
        const valuee = value.format(DATE_FORMAT).toString();
        const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
        return timeStampToDate.toLowerCase().includes(valuee.toLowerCase());
      }
      return record[field.key]?.toString().toLowerCase().includes(value.toLowerCase());
    },
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput && searchInput.select && searchInput.select());
      }
    },
  });
  const columns = Object.values(DATA_TABLE_COLUMNS_AGING_REPORT).map(field => {
    return {
      title: (
        <Tooltip title={field.title.length !== 0 ? getMessage(field?.title) : null} placement="top">
          <span>{field.title.length !== 0 ? getMessage(field?.title) : null}</span>
        </Tooltip>
      ),
      className: field?.className,
      children: field?.childs?.map(x => {
        return {
          title: (
            <Tooltip title={getMessage(x?.label)} placement="top" el>
              <span className="bg-grey-lightest">{getMessage(x?.label)}</span>
            </Tooltip>
          ),
          ellipsis: true,
          className: field?.className,
          dataIndex: x?.key,
          key: x?.key,
          sorter:
            field.key === DATA_TABLE_COLUMNS_AGING_REPORT.ALLOCATION_AGING.key
              ? null
              : (a, b) => Utils.antdTableSorter(a, b, field.key),
          sortDirections: SORT_DIRECTIONS,
          isVisible: visibility[x?.key],
          ...getColumnSearchProps(x),
          render: (_, record) => {
            if (x.type === "date") {
              if (!Utils.isEmpty(record[x?.key])) return moment(record[x?.key]).format(DATE_FORMAT);
            }
            return (
              <Tooltip title={record[x?.key]} placement="top">
                <span> {record[x?.key]}</span>
              </Tooltip>
            );
          },
        };
      }),
    };
  });

  const ChildColumns = Object.values(columns).map(field => {
    return field.children.map(x => {
      return x;
    });
  });
  const allColumns = ChildColumns.flat();
  const allDataTableColumns = Object.values(ALL_DATA_TABLE_COLUMNS_EMPLOYEESTATUS_REPORT).map(item => {
    return {
      key: item.key,
    };
  });

  const allColumnsForPdfExport = allColumns.concat(allDataTableColumns);
  const filteredColumns = useMemo(
    () =>
      columns.map(item => {
        item.children = item.children.filter(child => child.isVisible);
        return item;
      }),
    [visibility, loading],
  );

  const handleChange = (pagination, filters, sorter, extras) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };

  const handleSearch = (selectedKeys, search, dataIndex) => {
    search();
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    confirm();
  };

  const onSearch = async values => {
    if (!Utils.isEmpty(values)) {
      await fetchEmployeeAgingReport(values);
    }
  };

  const handleMenuVisibleChange = flag => {
    setIsMenuVisible(flag);
  };

  const onColHide = data => {
    const _visibility = { ...visibility, [data.key]: !visibility[data.key] };
    setVisibility({ ..._visibility });
  };

  const clearFilters = () => {
    // setFilteredInfo({});
    // setSortedInfo({});
    setSearchParams(defaultSearchParams);
    setVisibility(initialVisibleColumns);
    form.resetFields();
  };

  const onMenuItemClick = ({ key }) => {
    switch (key) {
      case MENU_ITEMS_TYPES.clearAllFilters:
        clearFilters();
        break;
      case MENU_ITEMS_TYPES.exportAllExcel:
      case MENU_ITEMS_TYPES.exportVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllPDF:
      case MENU_ITEMS_TYPES.exportVisiblePDF:
        exportToPDF(key);
        break;
      default:
        return;
    }
  };

  const exportToExcel = type => {
    let data;
    if (type === MENU_ITEMS_TYPES.exportAllExcel) {
      const allData = true;
      data = employeeAgingService.preparedDataForExport(EmployeeAgingReportData, allData);
    } else {
      const allData = false;
      data = employeeAgingService.preparedDataForExport(EmployeeAgingReportData, allData, visibility);
    }
    FileHandler.exportToExcelCustomHeader(data, FILE_NAME);
    setIsMenuVisible(false);
  };

  const exportToPDF = type => {
    const ChildColumnsHeaders = Object.values(filteredColumns).map(field => {
      return field.children.map(x => {
        return x;
      });
    });
    const Headers = ChildColumnsHeaders.flat();
    let data, width, headers;
    if (type === MENU_ITEMS_TYPES.exportAllPDF) {
      const allData = true;
      width = 1050;
      data = employeeAgingService.preparedDataForExport(EmployeeAgingReportData, allData);
      headers = employeeAgingService.preparedHeadersForPDF(allColumnsForPdfExport);
    } else {
      let allData = false;
      width = 550;
      data = employeeAgingService.preparedDataForExport(currentVisibleTableData, allData, visibility);
      headers = employeeAgingService.preparedHeadersForPDF(Headers);
    }
    FileHandler.exportToPDF(headers, data, { fontSize: 11, padding: 4 }, width, 300, FILE_NAME);
    setIsMenuVisible(false);
  };
  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };
  const fetchEmployeeAgingReport = async values => {
    setLoading(true);
    const params = {
      locations: values.city,
      accounts: values.account,
    };
    const response = await employeeAgingService.getReport(params);

    if (response) {
      setEmployeeAgingReportData(response);
    }
    setLoading(false);
  };
  const handleOpenChange = newOpen => {
    setOpenPopOver(newOpen);
  };
  return (
    <>
      <Row align="middle" className="mb-3">
        <Col xs={24} sm={12} md={12} lg={24} xl={24}>
          <PageHeader
            className="p-2 pl-2"
            title={getMessage("sidenav.reports.individual_status_report")}
            subTitle={
              <Popover
                placement="right"
                trigger="click"
                content={REPORTS_DESCRIPTION.employeeAgingReport}
                open={openPopOver}
                onOpenChange={handleOpenChange}
              >
                <QuestionCircleTwoTone className="font-size-md mb-0" />
              </Popover>
            }
          />
        </Col>
      </Row>
      <Form
        form={form}
        name={FORM_NAME}
        onFinish={onSearch}
        layout="horizontal"
        wrapperCol={{ span: 16 }}
        labelCol={{ span: 8 }}
        labelAlign="left"
      >
        {EmployeeAgingReportData.length === 0 ? null : (
          <Row justify="space-between" gutter={[20, 20]} className="mt-4">
            <Col lg={24} md={24}>
              <div className="ant-affix mt-2 menu-bar">
                <Button type="primary" onClick={showDrawer} shape="circle" className="mr-3">
                  <MenuFoldOutlined />
                </Button>
              </div>
            </Col>
          </Row>
        )}

        <Row gutter={[5, 5]} className="mb-3">
          <Col lg={8} md={12} xs={24}>
            <Form.Item name={ACCOUNT_TYPE.value} label={ACCOUNT_TYPE.text} rules={[{ required: true }]}>
              <Select
                showSearch
                optionFilterProp={CHILDREN}
                mode="multiple"
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.ACCOUNT.label)}
                onSelect={value => Utils.onAccountSelection(value, form, FILTER_FORM_FIELDS.ACCOUNT.name)}
              >
                <Option value={ALL}>{ALL}</Option>
                {accountsList?.map((account, index) => (
                  <Option key={index} value={account.accountName}>
                    {account.accountName}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col lg={8} md={8} xs={24}>
            <Form.Item
              name={FILTER_FORM_FIELDS.CITY.name}
              label={getMessage(FILTER_FORM_FIELDS.CITY.label)}
              rules={[{ required: true }]}
            >
              <LocationPicker
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.CITY.label)}
                locations={allLocations}
                name="city"
                form={form}
              />
            </Form.Item>
          </Col>
          <Col lg={4} md={24} xs={24}>
            <Button type="primary" icon={<SearchOutlined className="mr-2" />} htmlType="submit" className="float-right">
              {SEARCH.text}
            </Button>
          </Col>
        </Row>
      </Form>

      <Tabs type="card">
        <Tabs.TabPane tab="Report" key="report" className="pl-4 pr-3 pt-3">
          <TableComponent
            rowKey={(record, index) => `${record.employeeId}${index}`}
            columns={filteredColumns}
            data={EmployeeAgingReportData}
            scroll={{ x: true }}
            handleChange={handleChange}
            visibleData={data => {
              currentVisibleTableData = data;
            }}
            tableSize="small"
            visible={isMenuVisible}
            onDropdownMenuChange={handleMenuVisibleChange}
            loading={loading}
            showSorterTooltip={false}
          />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Summary" key="summary" className="p-3">
          <EmployeeAgingReportSummary />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Visual" key="visual" className="p-3">
          <EmployeeAgingReportVisual />
        </Tabs.TabPane>
      </Tabs>

      <Drawer onClose={onClose} visible={open} destroyOnClose width={250}>
        <TableMenuItem
          columns={allColumns}
          onColHide={onColHide}
          menuItems={MENU_ITEMS}
          onMenuItemClick={onMenuItemClick}
        />
      </Drawer>
    </>
  );
};

export default BenchReport;
